<template>
  <div>
    <PersonalHeader></PersonalHeader>
    <div class="centerBg">
      <div class="wrapper clearfix">
        <div class="centerLeft">
          <PersonalMenu class="fl" currentMenu="4"></PersonalMenu>
        </div>
        <div class="centerRight">
          <div class="centerTitle">
            <ul>
              <li class="active">我的留言</li>
            </ul>
          </div>
          <div class="footContent">
            <div  class="leaveRow" v-for="item in data" :key="'data'+item.id" >
              <dl class="clearfix">
                <dt><img :src="item.item.img"></dt>
                <dd>
                  <router-link :to="'/detail?id='+item.id">


                    <div class="title between">
                      <h4>{{item.item.item_name}}</h4>
                      <b>￥{{item.item.money}}万</b>
                    </div>
                    <p class="desc">
                      <span>所属行业：{{item.item.cate_name}}</span>
                      <span>直营店：{{item.item.shop_count}}家</span>
                      <span>申请人数：{{item.item.read_num}}</span>
                      <span>加盟店：{{item.item.fran_store_num}}家</span>
                    </p>
                  </router-link>
                </dd>
                <span class="lookStatus lookStatus1" :data-id="item.id" v-if="item.status == 0" @click.stop="urge">催促查看</span>
                <span class="lookStatus lookStatus3" v-else-if="item.status == 2">已查看</span>
                <span class="lookStatus lookStatus2" v-else-if="item.status == 3">催促查看</span>
              </dl>
              <p class="content">{{item.content}}</p>
            </div>


          </div>

          <el-pagination class="paging" background layout="prev, pager, next" :total="count" @current-change="pageChange"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api   from "@/api/personal-api"
  export default {
    name: "index.vue",
    data(){
      return{
        data:'',
        count: 0,
        page: 0
      }
    },

    mounted(){
      this.getData();
    },

    methods: {
      /**
       * 获取留言列表
       */
      getData: function(){
          const {page} =this;
          api.getLeaveList({page}).then(data=>{
            this.data = data.data;
            this.count = data.count;
          })
      },

      /**
       * 点击分页
       * @param e
       */
      pageChange:function(e){
        this.page = e-1;
        window.scrollTo(0,0);
        this.getData();
      },
      /**
       * 催促查看
       */
      urge: function({currentTarget:{dataset:{id}}}){
        api.urge({id}).then(data => {
          this.$message({message:'已为您提醒商家',type:"success"});
          this.getData();
        });
      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style lang="less" scoped>
  .centerBg{
    width: 100%;
    height: auto;
    padding: 20px 0 100px;
    background: #F4F5FA;
  }
  .centerLeft{
    float: left;
    width: 230px;
  }
  .centerRight{
    float: right;
    width: 950px;
    min-height: 610px;
    background: #fff;
    .centerTitle{
      padding: 0 33px;
      height: 60px;
      border-bottom: 1px solid #E3E4E5;
      ul{
        li{
          height: 58px;
          line-height: 58px;
          float: left;
          color: #333;
          font-size: 16px;
          border-bottom: 2px solid #fff;
        }
        li.active{
          border-bottom: 2px solid #4F74F4;
        }
      }
    }
    .footContent{
      padding: 0 30px;
      .leaveRow{
        padding-bottom:20px;
        border-bottom: 1px dashed #E3E4E5;
        dl{
          padding: 20px 0;
          position: relative;
          dt{
            float: left;
            width: 140px;
            height: 140px;
            img{
              display: block;
              width: 100%;
              height: 100%;
            }
          }
          dd{
            float: right;
            width: 730px;
            .title{
              height: 26px;
              line-height: 26px;
              font-size: 18px;
              margin: 20px 0;
              h4{
                color: #333;
              }
              b{
                color: #FF7E00;
              }
            }
            .desc{
              width: 330px;
              span{
                float: left;
                width: 160px;
                line-height: 28px;
                color: #999;
              }
            }
          }
          .lookStatus{
            width:80px;
            height:32px;
            line-height: 32px;
            text-align: center;
            border-radius:2px;
            color: #fff;
            position: absolute;
            right: 0;
            bottom: 20px;
            z-index: 10;
          }
          .lookStatus1{
            cursor: pointer;
            background:linear-gradient(0deg,rgba(79,116,244,1) 0%,rgba(87,158,250,1) 100%);
          }
          .lookStatus2{
            background:linear-gradient(0deg,rgba(79,116,244,1) 0%,rgba(87,158,250,1) 100%);
            opacity: 0.5;
            cursor: not-allowed;
          }
          .lookStatus3{
            width: 78px;
            height: 30px;
            line-height: 30px;
            background: #F4F5FA;
            border: 1px solid #E6E6E6;
            color: #999;
            cursor: not-allowed;
          }
        }
        .content{
          padding: 20px;
          color: #808080;
          line-height: 26px;
          background: #F4F5FA;
        }
      }

    }
  }
  .paging{
    margin: 30px;
    text-align: center;
  }
</style>



